<template>
  <div style="height: 100%;">
    <el-container>
      <!--头像-->
      <el-header>
        <!-- <img alt="" class="mlogo" src="../assets/yuuka.jpg"> -->
        <span style="margin-left: 20px"></span>
        <span> 博客后台登陆  </span>
        <el-divider direction="vertical"></el-divider>
        <span><el-link href="/" style="color: white; font-size: 22px; " underline="false">主页</el-link></span>
        
        
      </el-header>
      <!--主界面-->
      <el-main>
        <el-form ref="ruleForm" :model="ruleForm" :rules="rules" class="demo-ruleForm" label-width="100px" status-icon>
          <el-form-item label="用户名" prop="username">
            <el-input v-model="ruleForm.username" maxlength="12" type="text"></el-input>
          </el-form-item>
          <el-form-item label="密码" prop="password">
            <el-input v-model="ruleForm.password" autocomplete="off" type="password"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm')">登录</el-button>
            <el-button @click="resetForm('ruleForm')">重置</el-button>
          </el-form-item>
        </el-form>
      </el-main>
    </el-container>
  </div>
</template>

<script>
export default {
  name: 'Login',
  data() {
    var validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'));
      } else {
        callback();
      }
    };
    return {
      ruleForm: {
        password: '',
        username: ''
      },
      rules: {
        password: [
          {validator: validatePass, trigger: 'blur'}
        ],
        username: [
          {required: true, message: '请输入用户名', trigger: 'blur'},
          {min: 3, max: 12, message: '长度在 3 到 12 个字符', trigger: 'blur'}
        ]
      }
    };
  },
  methods: {
    //登录
    submitForm(formName) {
      const _this = this
      this.$refs[formName].validate((valid) => {
        if (valid) {
          // 提交逻辑
          console.log('Request headers:', this.$axios.defaults.headers.common); // 打印默认请求头

          this.$axios.post('/login', this.ruleForm).then((res) => {
            const token = res.headers['authorization']
            console.log('token: ', token)
            _this.$store.commit('SET_TOKEN', token)
            _this.$store.commit('SET_USERINFO', res.data.data)
            _this.$router.push("/admin")
          })
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    //清空用户名密码
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  },
  mounted() {
  }
}
</script>

<style scoped>
.el-header, .el-footer {
  background-color: rgb(60, 128, 231);
  color: #333;
  /* text-align: center; */
  display: flex;
  /* line-height: 60px; */
  color: #ffffff;
  font-size: 22px;
  padding-left: 10px;
  align-items: center;
  user-select: none;

}
.el-aside {
  background-color: #D3DCE6;
  color: #333;
  text-align: center;
  line-height: 200px;
}
.el-main {
  background-color: #ffffff;
  color: #3f3f3f;
  text-align: center;
  line-height: 160px;
  height: 30%;
}
 .el-container {
  margin-bottom: 40px;
  height: 100%;
}
.mlogo {
  height: 60%;
  margin-top: 10px;
}
.demo-ruleForm {
  max-width: 500px;
  margin: 0 auto;
}
</style>